.preloader {
  width: 100%;
  height: 100%;
  z-index: 3;
  opacity: 1;
  visibility: visible;
  background-color: #e6f3f8;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader .loader {
  width: 75px;
  height: 75px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(50%, 50%);
}

.preloader .loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #4440f6;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

