.favorite__section {
z-index: -1;
padding: 40px 0;
position: relative;

@media screen and (min-width: 768px) {
    padding: 48px 0;
}

@media screen and (min-width: 1280px) {
    padding: 60px 0;
}
}


.error {
padding: 40px 0 60px 0;
margin-left: auto;

&__container {
display: flex;
flex-direction: column;
align-items: center;
}

&___title {
margin-bottom: 40px;

font-weight: 700;
font-size: 20px;
line-height: 1.35;
text-align: center;
letter-spacing: -0.01em;

        @media screen and (min-width: 768px) {
            font-size: 40px;
            line-height: 1.4;
        }
    }

    &___picture {
        width: 248px;

        @media screen and (min-width: 768px) {
            width: 560px;
        }

        @media screen and (min-width: 1280px) {
            width: 601px;
        }
    }
}

.is-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}